.wrap{
    display: flex;
    gap: 7px;
    align-items: center;
    position: relative;
}
.graphIcon{
    width: 21px;
    height: 21px;
    cursor: pointer;
    display: block;
    filter: invert(30%) sepia(86%) saturate(3041%) hue-rotate(219deg) brightness(100%) contrast(104%);
}
.graphIconDisabled{
    width: 21px;
    cursor: pointer;
    height: 21px;
    display: block;
    filter: invert(98%) sepia(1%) saturate(5%) hue-rotate(18deg) brightness(102%) contrast(101%);
}
.active{
    padding: 7px;
    background-color: #2a2e39;
    display: flex;
}
.disabled{
    padding: 7px;
    border-radius: 6px;  
    display: flex;
}
.presetWrapIcon{
    display: flex;
    align-items: center;
    justify-content: center;
}
.icons{
    display: flex;
    gap: 4px;
}
.disabled:hover{
    background-color: #2a2e39;
}
