.row{
    display: flex;
    color: #d1d4dc;
    align-items: center;
}
.row>div{
    width: 14.28%;
    font-size: 13px;
    font-weight: 600;
    padding: 5px 13px;
    text-align: start;
    position: relative;
}
.filterWithIcon svg{
    color: #fff;
}
.filterWithIcon svg:hover{
    color: #2962ff;
}
.row>div:first-child span{
    padding: 0px;
}
.filterWithIcon{
    display: flex;
    align-items: center;
    gap: 1px;
}