.wrap{
    border: 1px solid  #2a2e39;
    flex-basis: 100%;
    height: 100%;
    color: #fff;
    display: flex;
    flex-direction: column;
    padding: 0px 3px 3px 0px;
    background-color: #161a25;
}
.wrapActive{
    border: 1px solid  #2a2e39;
    flex-basis: 100%;
    height: 100%;
    color: #fff;
    overflow: hidden;
    padding: 0px 3px 3px 0px;
    cursor: crosshair;
    display: flex;
    flex-direction: column;
    background-color: #161a25;
}
.headerGraph{
    color:#d1d4dc;
    font-size: 13px;
    flex-wrap: wrap;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    cursor: default;
}
.groups{
    display: flex;
    align-items: center;
    gap: 3px;
}
.info{
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
    height: min-content;
    align-items: center;
    color:#737478;
    padding-left: 3px;
}
.info p{
    font-weight: 100;
    font-size: 11px;
}
.headerGraph svg{
    font-size: 18px;
}
.leftHand{
    display: flex;
    gap: 5px;
    align-items: center;
}
.wideS{
    cursor: pointer;
}
.dot{
    color: rgb(5, 241, 5);
    cursor: default;
   
}
.addPersonal{
    font-size: 20px;
    cursor: pointer;
}
.graphic{
    display: flex;
    flex-basis: 100%;
    align-items: center;
    justify-content: center;
}
.graphicAdd{
    display: flex;
    flex-basis: 100%;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.graphicAdd svg{
    font-size: 35px;
    cursor: pointer;
}
.coin{
    color: #d1d4dc;
}
.coin:hover{
    color:#b9b9b9;
    cursor: pointer;
}
.futures{
    color: green;
}
.spot{
    color:#60b8ca
}